import React, { Fragment } from 'react';
//import './style.css';
import { List, Card } from 'antd';
const { Meta } = Card;

const data = [
  {
    id: 1,
    title: 'Demo product',
    price: 1000,
    img: 'http://www.gesgi.com/wp-content/uploads/2014/05/demo-image-325x390.png'
  },
  {
    id: 2,
    title: 'Demo product',
    price: 200,
    img: 'http://www.gesgi.com/wp-content/uploads/2014/05/demo-image-325x390.png'
  },
  {
    id: 3,
    title: 'Demo product',
    price: 300,
    img: 'http://www.gesgi.com/wp-content/uploads/2014/05/demo-image-325x390.png'
  },
  {
    id: 4,
    title: 'Demo product',
    price: 100,
    img: 'http://www.gesgi.com/wp-content/uploads/2014/05/demo-image-325x390.png'
  },
];

const PopularProducts = (props) => (
    <Fragment>
        <h2 style={{
        }}>{props.title}</h2>
        <hr style={{borderTop: '1px solid #deeeef'}}/>
        <List
        style={{marginTop: 20}}
        grid={{ gutter: 16, column: 4 }}
        dataSource={data}
        renderItem={item => (
            <List.Item>
                <Card
                hoverable
                style={{ width: 240 }}
                cover={<img alt="example" src={item.img} />}
              >
                <Meta
                  title={item.title}
                  description={"price: $"+item.price}
                />
              </Card>
            </List.Item>
    
    )}/>
    </Fragment>
)

export default PopularProducts


