import React, { Component, Fragment } from 'react';
import { Carousel, Button } from 'antd';
import './style.css';
const sliderData = [
  {
    id : "5a6b5e595e4a754ce7ea3c5b",
    imagePath : "https://storeinua.com/image/cache/iphone/iphone%20x/iphone%20x%20silver-900x900.png",
    title : "iPhone X",
    description : "Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque perspiciatis deleniti quam est unde ad non quidem iste blanditiis, maxime fugit, tempore dolorem voluptatem commodi, dignissimos sequi praesentium architecto accusantium!"
  },
  {
    id : "5a6b5e595e4a754ce7ea3c5e",
    imagePath : "https://www.pcexpress.lu/wp-content/uploads/2016/12/macbook.png",
    title : "MacbookPro 13",
    description : "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Neque laborum doloremque, mollitia excepturi voluptas commodi, distinctio laudantium nulla quisquam tenetur sed cum natus. Quae, quidem illo? Dolores cum necessitatibus omnis."
  },
  {
    id : "5a6b5e595e4a754ce7ea3c60",
    imagePath : "https://iglaz.com.ua/image/cache/catalog/products/ipad-air-2-silver_2-1200x800.png",
    title : "iPad",
    description : "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Neque laborum doloremque, mollitia excepturi voluptas commodi, distinctio laudantium nulla quisquam tenetur sed cum natus. Quae, quidem illo? Dolores cum necessitatibus omnis."
  }
]

class SliderComponent extends Component {
  render() {
    return (
      <Fragment>
        <Carousel autoplay autoplaySpeed='5000'>
           {
             sliderData.map(item => (
               <div className="wrapper" key={item.id}>
              <div className="col-1">
              <div className="product-wrap">
                <div className="product-shot">
                  <img src={item.imagePath} alt="" />
                </div>
              </div>
            </div>
            
            <div className="col-2">
              <div className="product-info">
                <h2>{item.title}</h2>
                <div className="desc">
                  {item.description}
                </div>
               <Button style={{
                 marginTop: 10
               }} type="primary">Add to cart</Button>
               <Button style={{
                 marginLeft: 10
               }}>More info</Button>
              </div>
              
            </div>
            </div>
             ))
           }
        </Carousel>
      </Fragment>
    );
  }
}

export default SliderComponent;



